body {
  font-family: "Lato", sans-serif;
}
/*Navbar */

#Header {
  background-color: #b172a8;
}

/* Home Page */

#homeContainer {
  background-color: #9b5c9b;
  text-align: center;
  align-content: center;
  margin-top: 90px;
  color: #fff;
}

#profilePicCol {
  text-align: center;
  max-width: 30%;
}

#ProfilePicHome {
  max-width: 90%;
  min-width: 50%;
}

/*About Page */

#profilePic {
  max-width: 90%;
  margin-bottom: 10px;
}

#cardImage {
  max-width: 70%;
  min-width: 70%;
  max-height: 85%;
  min-height: 80%;
  margin: 20px 5px 5px 5px;
  border: #000 solid 1px;
}

/* .card-set {
  text-align: center;
} */

.card {
  border: #000 solid 1px;
  width: 90%;
  height: 90%;
  background-color: #000;
  color: #ffffff;
}

.card:hover {
  border: #b97fb9
  
  
  
  
  
  
  solid 5px;
  width: 90%;
  height: 90%;
  background-color: #444444;
  color: #ffffff;
}

.anchor-profile {
  max-width: 75%;
  min-width: 75%;
}

#cardTextLink {
  text-decoration: underline;
  color: #b97fb9
  
  
  
  
  
  
  ;
}

/* #cardTextLink:hover {
  text-decoration: underline;
  color: #e7eb0e;
} */

#profileCard {
  align-content: center;
  text-align: center;
  margin-top: 10%;
}

#portfolioCol {
  text-align: center;
  align-content: center;
  align-items: center;
}

#portfolioRow {
  text-align: center;
}

#contactIcons {
  max-width: 10%;
  min-width: 10%;
}

#cardAlbum {
  max-height: 50%;
  min-height: 50%;
  background-color: #444;
}

#contactContainer {
  margin-left: 20px;
  margin-right: 20px;
  align-items: center;
  text-align: center;
  background-color: #444;
  color: #ffffff;
}

#footerInvite {
  font-size: larger;
  margin-left: 5px;
}

#FootLink {
  text-decoration: underline;
  font-size: larger;
  margin-left: 5px;
}


#FootLink:hover {
  text-decoration: underline;
  font-size: larger;
  margin-left: 5px;
  color: #000;
}

#aboutHeadRow {
  text-align: center;
}

#aboutHeadCol2 {
  margin-right: 30px;
}

#aboutContainer {
  margin-left: 0px;
  margin-right: 0px;
  text-align: center;
  background-color: #b97fb9;
  color: #fff;
}

#contact {
  margin-left: 10px;
  margin-right: -25px;
}

#portfolio {
  margin-left: 30px;
  margin-right: -20px;
  align-items: center;
  text-align: center;
}

#navbarLink {
  font-size: 100%;
  text-decoration: underline;
  color: #444;
  /* align-items: center; */
}

#navbarLink:hover {
  font-size: 100%;
  text-decoration: underline;
  color:#b97fb9;
  /* align-items: center; */
}

#FootL {
  flex-shrink: 0;
  color: black;
}
/* CSS for small mobile horizontal screen */

@media only screen and (max-height: 350px) and (orientation: landscape) {
  #header {
    padding-bottom: -10px !important;
    padding-top: -10px !important;
  }
}

#Footer {
  flex-shrink: 0;
  color: black;
}

#footerIconLink {
  max-width: 5%;
}

#footerLink {
  max-width: 40%;
}

#Resume {
  margin-top: 110px;
  max-width: 100%;
  max-height: 100%;
  text-align: center;
  background-color: #b172a8;
  color: #fff;
}
#ResumeLink {
  text-decoration: underline;
  color: black;
}
#ResumeLink:hover {
  text-decoration: underline;
  color: #000;
}

#DocumentLink {
  max-width: 15%;
}

